/* */
import { keepTrackApi } from '@app/js/api/keepTrackApi';
import $ from 'jquery';
let isLaunchMenuOpen = true;
export const init = () => {
    // Add HTML
    keepTrackApi.register({
        method: 'uiManagerInit',
        cbName: 'launchCalendar',
        cb: uiManagerInit,
    });
    // Add JavaScript
    keepTrackApi.register({
        method: 'bottomMenuClick',
        cbName: 'launchCalendar',
        cb: bottomMenuClick,
    });
    // Might be unnecessary
    keepTrackApi.register({
        method: 'hideSideMenus',
        cbName: 'launchCalendar',
        cb: hideSideMenus,
    });
};
export const bottomMenuClick = (iconName) => {
    if (iconName === 'menu-launches') {
        if (isLaunchMenuOpen) {
            isLaunchMenuOpen = false;
            keepTrackApi.programs.uiManager.hideSideMenus();
            return;
        }
        else {
            if (settingsManager.isMobileModeEnabled)
                keepTrackApi.programs.uiManager.searchToggle(false);
            settingsManager.isPreventColorboxClose = true;
            setTimeout(function () {
                settingsManager.isPreventColorboxClose = false;
            }, 2000);
            keepTrackApi.programs.uiManager.hideSideMenus();
            try {
                if (location.protocol === 'https:') {
                    $.colorbox({
                        href: 'https://space.skyrocket.de/doc_chr/lau2020.htm',
                        iframe: true,
                        width: '80%',
                        height: '80%',
                        fastIframe: false,
                        closeButton: false,
                    });
                }
                else {
                    $.colorbox({
                        href: 'http://space.skyrocket.de/doc_chr/lau2020.htm',
                        iframe: true,
                        width: '80%',
                        height: '80%',
                        fastIframe: false,
                        closeButton: false,
                    });
                }
            }
            catch (error) {
                // console.debug(error);
            }
            isLaunchMenuOpen = true;
            $('#menu-launches').addClass('bmenu-item-selected');
            return;
        }
    }
};
export const hideSideMenus = () => {
    $('#menu-launches').removeClass('bmenu-item-selected');
};
export const cboxClosed = () => {
    if (isLaunchMenuOpen) {
        isLaunchMenuOpen = false;
        $('#menu-launches').removeClass('bmenu-item-selected');
    }
};
export const uiManagerInit = () => {
    // Bottom Icon
    $('#bottom-icons').append(keepTrackApi.html `
    <div id="menu-launches" class="bmenu-item">
      <img alt="calendar2" src="" delayedsrc="data:image/png;base64,">
      <span class="bmenu-title">Launch Calendar</span>
      <div class="status-icon"></div>
    </div> 
  `);
    $(document).on('cbox_closed', cboxClosed);
};
